import { Controller } from '@ali/4ever-cangjie';
import { ALIGNMENT_SET } from '../actions';
import setAlignment from '../commands/setAlignment';

export default function onAction(action, controller: Controller, next: Function) {
  const { type, payload } = action;
  if (type === ALIGNMENT_SET) {
    const { align, key } = payload;
    return setAlignment(controller, align, key);
  }
  return next();
}
